.underline-em {
    font-weight: bold;
    font-style: italic;
    color: #760c0c;
    text-decoration: underline;
}

/* 컨테이너 (div) 안에 이미지 가로 크기 비율 적용 */
.thumbnail-wrappper {
    width: 50%;
}

/* 퍼센테이지(%, 백분율)은 컨테이너 블럭의 너비를 참조 */
/* 자식 패딩요소는 부모의 가로 크기에 영향을 받음 : 브라우저 공통사항 */
.thumbnail-wrappper .thumbnail {
    position: relative;
    /* 1:0.8 비율 : 패딩으로 비율 주기 가능 */
    padding-top: 80%; 
    overflow: hidden;
}

.thumbnail-wrappper .thumbnail img {
    position: absolute;
    top:0;
    left:0;
    max-width: 100%;
    height: auto;
}